<h2 id="块级元素" tabindex="-1"><a class="header-anchor" href="#块级元素" aria-hidden="true">#</a> 块级元素</h2>
<p><strong>BLOCK ELEMENTS</strong></p>
<h3 id="短语和段落" tabindex="-1"><a class="header-anchor" href="#短语和段落" aria-hidden="true">#</a> 短语和段落</h3>
<p><strong>PARAGRAPHS AND LINE BREAKS</strong></p>
<p>段落只是一行或多行连续的文本，由一行或多行空白行隔开。空行是指任何看起来像空行的行 - 除了空格或制表符之外什么都不包含的行被视为空白。</p>
<p>当您确实想使用Markdown插入一个<code>&lt;br/&gt;</code>中断标记时，可以用<strong>两个或更多空格</strong>结束一行，然后键入return。</p>
<p>  首行缩进两行使用两个<code>&amp;nbsp;&amp;nbsp;</code></p>
<p><strong>举个例子</strong></p>
<p>人在世上，无论做什么事情，<strong>都要脚踏实地，一步一个脚印</strong>，一小步，一小步，循序渐进，逐渐去完成。<br>
要胸有大志，做事都要有计划，有目标。不能盲干、瞎干、乱干 ，要坚持不懈、朝着目标努力，并且在前行的路上，不畏艰辛，不怕坎坷崎岖，最终才能达到目的。<br>
<strong>千里之行，始于足下</strong>。任何人急于求成，或者想不吃苦，想投机取巧，走捷径达到自己的人生目标，都只能是事与愿违。<br>
<strong>骐骥一跃，不能十步；驽马十驾，功在不舍</strong>。任何成大事者，在成功的路上都会步步为营，三思而后行。每一个计划，每一个步骤，每一个环节都会缜密考量，然后逐一渐进式的付诸行动，并且时时摸索经验，总结教训，日积月累，从小做大，再做强，最后事业才能蒸蒸日上，达到辉煌的顶峰。</p>
<h3 id="标题" tabindex="-1"><a class="header-anchor" href="#标题" aria-hidden="true">#</a> 标题</h3>
<p><strong>HEADERS</strong></p>
<p>Markdown支持两种样式的标题</p>
<ul>
<li>Setext-style</li>
</ul>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token title important">This is an H1
<span class="token punctuation">=============</span></span>

<span class="token title important">This is an H2
<span class="token punctuation">-------------</span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><ul>
<li>Atx-style</li>
</ul>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token title important"><span class="token punctuation">#</span> This is an H1</span>

<span class="token title important"><span class="token punctuation">##</span> This is an H2</span>

<span class="token title important"><span class="token punctuation">######</span> This is an H6 <span class="token punctuation">######</span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="引用" tabindex="-1"><a class="header-anchor" href="#引用" aria-hidden="true">#</a> 引用</h3>
<p><strong>BLOCKQUOTES</strong></p>
<p>Markdown使用电子邮件样式 <strong>&gt;</strong> 字符进行分块引用。只需要将 <strong>&gt;</strong> 放在包装段落的第一行即可。</p>
<blockquote>
<p>青，取之于蓝，而青于蓝；冰，水为之，而寒于水。木直中绳，輮以为轮，其曲中规。虽有槁暴，不复挺者，輮使之然也。故木受绳则直，金就砺则利，君子博学而日参省乎己，则知明而行无过矣。</p>
</blockquote>
<blockquote>
<p>故不登高山，不知天之高也；不临深溪，不知地之厚也；不闻先王之遗言，不知学问之大也。干、越、夷、貉之子，生而同声，长而异俗，教使之然也。诗曰：“嗟尔君子，无恒安息。靖共尔位，好是正直。神之听之，介尔景福。”神莫大于化道，福莫长于无祸。</p>
</blockquote>
<blockquote>
<p>故不登高山，不知天之高也</p>
<blockquote>
<p>不临深溪，不知地之厚也</p>
</blockquote>
<p>不闻先王之遗言，不知学问之大也</p>
</blockquote>
<p>Blockquotes可以包含其他降价元素，包括标题、列表和代码块。</p>
<h3 id="列表" tabindex="-1"><a class="header-anchor" href="#列表" aria-hidden="true">#</a> 列表</h3>
<p><strong>LISTS</strong></p>
<p>Markdown支持有序（编号）和无序（项目符号）列表。</p>
<p>无序列表使用 <code>+</code>,<code>*</code>,<code>-</code></p>
<ul>
<li>Red</li>
<li>Green</li>
<li>Blue</li>
</ul>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token list punctuation">*</span>   Red
<span class="token list punctuation">*</span>   Green
<span class="token list punctuation">*</span>   Blue
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>有序列表使用数字</p>
<ol>
<li>Red</li>
<li>Green</li>
<li>Blue</li>
</ol>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token list punctuation">1.</span>  Red
<span class="token list punctuation">2.</span>  Green
<span class="token list punctuation">3.</span>  Blue
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><ul>
<li>积土成山，风雨兴焉；积水成渊，蛟龙生焉；积善成德，而神明自得，圣心备焉。故不积跬步，无以至千里；不积小流，无以成江海。</li>
<li>骐骥一跃，不能十步；驽马十驾，功在不舍。锲而舍之，朽木不折；锲而不舍，金石可镂。蚓无爪牙之利，筋骨之强，上食埃土，下饮黄泉，用心一也。蟹六跪而二螯，非蛇鳝之穴无可寄托者，用心躁也。</li>
</ul>
<p>如果列表项用空行分隔，Markdown将在HTML输出中将这些项包装在<code>&lt;p&gt;</code>标记中。例如</p>
<ul>
<li>
<p>Bird</p>
</li>
<li>
<p>Magic</p>
</li>
<li>
<p>This is a list item with two paragraphs.</p>
<p>This is the second paragraph in the list item. You're only required to indent the first line. Lorem ipsum dolor sit amet, consectetuer adipiscing elit.</p>
</li>
<li>
<p>Another item in the same list.</p>
</li>
</ul>
<h3 id="代码块" tabindex="-1"><a class="header-anchor" href="#代码块" aria-hidden="true">#</a> 代码块</h3>
<p><strong>CODE BLOCKS</strong></p>
<p>要在Markdown中生成代码块，只需将代码块的每一行缩进至少4个空格或1个制表符。</p>
<p>This is a normal paragraph:</p>
<pre><code>This is a code block.

&lt;div class=&quot;footer&quot;&gt;
    &amp;copy; 2004 Foo Corporation
&lt;/div&gt;
</code></pre>
<h3 id="水平线" tabindex="-1"><a class="header-anchor" href="#水平线" aria-hidden="true">#</a> 水平线</h3>
<p><strong>HORIZONTAL RULES</strong></p>
<hr>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token hr punctuation">* * *</span>

<span class="token hr punctuation">***</span>

<span class="token hr punctuation">*****</span>

<span class="token hr punctuation">- - -</span>

<span class="token hr punctuation">---------------------------------------</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><h2 id="行内元素" tabindex="-1"><a class="header-anchor" href="#行内元素" aria-hidden="true">#</a> 行内元素</h2>
<p><strong>SPAN ELEMENTS</strong></p>
<h3 id="链接" tabindex="-1"><a class="header-anchor" href="#链接" aria-hidden="true">#</a> 链接</h3>
<p><strong>LINKS</strong></p>
<p>Markdown支持两种类型的链接：<strong>内联</strong>和<strong>引用</strong>。</p>
<ul>
<li>内联</li>
</ul>
<p>带有标题的连接 <a href="http://www.baidu.com/" title="百度地址" target="_blank" rel="noopener noreferrer">百度一下<OutboundLink/></a> 百度一下你就知道.</p>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token url">[<span class="token content">百度一下</span>](<span class="token url">http://www.baidu.com/</span> <span class="token string">"百度地址"</span>)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>引用</li>
</ul>
<p>这是百度的地址 <a href="https://www.baidu.com" title="百度一下" target="_blank" rel="noopener noreferrer">百度一下<OutboundLink/></a> 百度一下你就知道</p>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token url">[<span class="token content">百度一下</span>][<span class="token variable">baidu</span>]</span>
<span class="token url-reference url"><span class="token punctuation">[</span><span class="token variable">baidu</span><span class="token punctuation">]</span><span class="token punctuation">:</span> https://www.baidu.com <span class="token string">"百度一下"</span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>I get 10 times more traffic from <a href="http://google.com/" title="Google" target="_blank" rel="noopener noreferrer">Google<OutboundLink/></a> than from
<a href="http://search.yahoo.com/" title="Yahoo Search" target="_blank" rel="noopener noreferrer">Yahoo<OutboundLink/></a> or <a href="http://search.msn.com/" title="MSN Search" target="_blank" rel="noopener noreferrer">MSN<OutboundLink/></a>.</p>
<h3 id="加强语气" tabindex="-1"><a class="header-anchor" href="#加强语气" aria-hidden="true">#</a> 加强语气</h3>
<p><strong>EMPHASIS</strong></p>
<p>加粗使用<code>**</code>或者<code>__</code>，斜体使用<code>*</code>或者<code>_</code></p>
<p><em>single asterisks</em></p>
<p><em>single underscores</em></p>
<p><strong>double asterisks</strong></p>
<p><strong>double underscores</strong></p>
<p>un<strong>frigging</strong>believable</p>
<p>Markdown中显示*使用<code>\*</code></p>
<p>*this text is surrounded by literal asterisks*</p>
<h3 id="代码" tabindex="-1"><a class="header-anchor" href="#代码" aria-hidden="true">#</a> 代码</h3>
<p><strong>CODE</strong></p>
<p>要指示代码的范围，请用反引号（`）将其括起来。</p>
<p>Use the <code>printf()</code> function.</p>
<p>如果代码里面有 <strong>`</strong>，就使用多个 <strong>``</strong></p>
<p><code>There is a literal backtick (`) here.</code></p>
<ul>
<li>
<p>A single backtick in a code span: <code>`</code></p>
</li>
<li>
<p>A backtick-delimited string in a code span: <code>`foo`</code></p>
</li>
<li>
<p>Please don't use any <code>&lt;blink&gt;</code> tags.</p>
</li>
</ul>
<h3 id="图片" tabindex="-1"><a class="header-anchor" href="#图片" aria-hidden="true">#</a> 图片</h3>
<p><strong>IMAGES</strong></p>
<p>Markdown使用的图像语法与链接的语法类似，允许两种样式：<strong>内联</strong> 和 <strong>引用</strong>。</p>
<ul>
<li>内联</li>
</ul>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token url"><span class="token operator">!</span>[<span class="token content">Alt text</span>](<span class="token url">/example.jpeg</span>)</span>
<span class="token url"><span class="token operator">!</span>[<span class="token content">Alt text</span>](<span class="token url">/example.jpeg</span> <span class="token string">"Optional title"</span>)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p><img src="/example.jpeg" alt="Alt text" title="Optional title"></p>
<ul>
<li>引用</li>
</ul>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code><span class="token url"><span class="token operator">!</span>[<span class="token content">Alt text</span>][<span class="token variable">id</span>]</span>
<span class="token url-reference url"><span class="token punctuation">[</span><span class="token variable">id</span><span class="token punctuation">]</span><span class="token punctuation">:</span> /example.jpeg  <span class="token string">"Optional title attribute"</span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p><img src="/example.jpeg" alt="Alt text" title="Optional title attribute"></p>
<h2 id="其他" tabindex="-1"><a class="header-anchor" href="#其他" aria-hidden="true">#</a> 其他</h2>
<p><strong>MISCELLANEOUS</strong></p>
<h3 id="自动连接" tabindex="-1"><a class="header-anchor" href="#自动连接" aria-hidden="true">#</a> 自动连接</h3>
<p><strong>AUTOMATIC LINKS</strong></p>
<p>只需用尖括号将URL或email地址括起来 <code>&lt;http://www.baidu.com/&gt;</code><br>
<a href="http://www.baidu.com/" target="_blank" rel="noopener noreferrer">http://www.baidu.com/<OutboundLink/></a></p>
<h3 id="反斜杠转义" tabindex="-1"><a class="header-anchor" href="#反斜杠转义" aria-hidden="true">#</a> 反斜杠转义</h3>
<p><strong>BACKSLASH ESCAPES</strong></p>
<p>Markdown允许使用反斜杠转义符来生成文本字符，否则这些字符在Markdown的格式语法中具有特殊含义</p>
<p>*literal asterisks*</p>
<p>Markdown为以下字符提供反斜杠转义符：</p>
<div class="language-markdown ext-md line-numbers-mode"><pre v-pre class="language-markdown"><code>\   backslash
`   backtick
<span class="token list punctuation">*</span>   asterisk
_   underscore
{}  curly braces
[]  square brackets
()  parentheses
<span class="token title important"><span class="token punctuation">#</span>   hash mark</span>
<span class="token list punctuation">+</span>   plus sign
<span class="token list punctuation">-</span>   minus sign (hyphen)
.   dot
!   exclamation mark
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div><h3 id="内联html" tabindex="-1"><a class="header-anchor" href="#内联html" aria-hidden="true">#</a> 内联HTML</h3>
<p><strong>INLINE HTML</strong></p>
<p>For example, to add an HTML table to a Markdown article:</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>table</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>tr</span><span class="token punctuation">></span></span>
        <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>td</span><span class="token punctuation">></span></span>Foo<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>td</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>tr</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>tr</span><span class="token punctuation">></span></span>
        <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>td</span><span class="token punctuation">></span></span>Foo<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>td</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>tr</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>tr</span><span class="token punctuation">></span></span>
        <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>td</span><span class="token punctuation">></span></span>Foo<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>td</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>tr</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>table</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><table>
    <tr>
        <td>Foo</td>
    </tr>
    <tr>
        <td>Foo</td>
    </tr>
    <tr>
        <td>Foo</td>
    </tr>
</table>
<h3 id="特殊字符转义" tabindex="-1"><a class="header-anchor" href="#特殊字符转义" aria-hidden="true">#</a> 特殊字符转义</h3>
<p><strong>AUTOMATIC ESCAPING FOR SPECIAL CHARACTERS</strong></p>
<p>在HTML中，有两个字符需要特殊处理：&lt;和&amp;。但是，内部标记代码的范围和块范围，尖括号和和符号总是自动编码的。这使得使用Markdown编写HTML代码变得很容易。</p>
<p>4 &lt; 5</p>
<p>AT&amp;T</p>
